.navigation {
  @include font($size: 2.0rem, $height: 5.0rem);
  display: flex;

  @include media-tablet {
    display: none;
  }
}

.navigation >a {
  text-decoration: none;
  color: $color-light;
}

.navigation__link {
  @include hover-link;
  cursor: pointer;
  list-style: none;
  padding-right: 30px;
}

